.form {
  background-color: #eee;
  padding: 1rem;
  margin: 0.5rem;
}
.data {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
label {
  text-align: center;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0.5rem;
}
input {
  padding: 0.2rem 0.5rem;
  font-size: 1rem;
  margin: 0.5rem;
  width: 80%;
}
