* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.card {
  background-color: #242424;
  margin: auto;
  padding: 2rem;
  width: fit-content;
  border-radius: 7px;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  max-width: 350px;
  min-width: 300px;
}
.text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
img {
  border-radius: 50%;
  width: 150px;
  height: 150px;
}
.name {
  text-align: center;
  color: #eee;
  font-weight: 500;
  margin: 0rem;
  font-size: 2rem;
  margin: 0.5rem;
}
* {
  /*outline:2px solid red;*/
}
.post {
  font-size: 1.4rem;
  font-weight: 100;
  color: #eee;
  text-align: center;
}
.date {
  font-size: 0.8rem;
  font-weight: 100;
  margin: 0.5rem;
  color: #999;
}
